const uni = require('uni').default;


































































var app = require("../../common/common.js");
import mtFooter from "../../components/footer.vue";
export default {
	components: {
		mtFooter
	},
	data: {
		pageLoad: false,
		pageHide: false,
		pageData: {}
	},

	onLoad: function (option) {
		uni.showNavigationBarLoading();
		this.getPage();
	},
	onReady: function () {
		uni.setNavigationBarTitle({
			title: "uniBbs",
		});
	},
	onShow: function () {
		if (this.pageHide) {
			this.pageHide = false;
			this.getPage();
		}
	},
	onHide: function () {
		this.pageHide = true;
	},
	methods: {
		getPage: function () {
			var that = this;
			uni.request({
				url: app.apiHost + "?ajax=1&fromapp=" + app.fromapp(),
				success: function (data) {
					that.pageLoad = true;
					that.pageData = data.data.data;
					uni.hideNavigationBarLoading();
				}
			})
		},

		goForum: function (id) {
			uni.navigateTo({
				url: "/pages/forum/show?id=" + id
			})
		}
	},
}



